.product-tile {
  position: relative;
  &--holder {
    background-color: $cl-bg;
  }
}

body:not(.touch-device) {
  .product-tile {
    &--sizes {
      opacity: 0;
      @include trn-03;
    }
    &:hover {
      .product-tile--sizes {
        opacity: 1;
      }
    }
  }
}

@media (min-width: 768px) {
  .product-tile {
    &--holder {
      position: absolute;
      left: 0;
      bottom: 0;
      background-color: transparent;
    }
  }
}