html {
	font-size: 16px;
	scroll-behavior: smooth;
}

body {
	@include txt-main;
	color: $cl-main;
	background-color: $cl-bg;
	overflow-x: hidden;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	position: relative;
}

::-webkit-scrollbar {
	width: 0.5rem;
}

::-webkit-scrollbar-thumb {
  background: $cl-muted;
}

::selection {
  color: $cl-white;
  background: $cl-main;
} 

abbr[title],
abbr[data-original-title] { 
	text-decoration: none;
}

a {
	color: $cl-main;
	@include trn-03;
	&:hover {
		text-decoration: none;
	}
}

.rtl-trick {
	direction: rtl;
	.rtl-reset {
		direction: ltr;
	}
}

.tl-p-t {
	padding-top: 1.25rem !important;
}

.tl-p-b {
	padding-bottom: 1.25rem !important;
}

.tl-p-y {
	padding-top: 1.25rem !important;
	padding-bottom: 1.25rem !important;
}

.tl-p-x {
	padding-left: 1.25rem !important;
	padding-right: 1.25rem !important;
}

.tl-p-big {
	padding: 1.25rem !important;
	@media (min-width: 768px) {
		padding: 2.5rem !important;
	}
}

.tl-pad {
	padding-left: 1.25rem !important;
	padding-right: 1.25rem !important;
}

.container {
	&.mod-content {
		max-width: 750px;
	}
	&.mod-tiny {
		max-width: 820px;
	}
}

.relative-index-1 {
	position: relative;
	z-index: 1;
}

.svg-icon {
  max-width: 1rem;
  max-height: 1rem;
}

.tl-hr {
	border: 0;
	border-top: 1px solid $cl-line;
}

.bord-rad-1 {
	border-radius: 0.1875rem;;
}

.border-top {
	border-top: 1px solid $cl-line;
}

.list-reset {
	@include list-reset;
}

.sticky-to-header {
	position: sticky;
	top: $header-mob-h;
	@media (min-width: 768px) {
		top: $header-desk-h;
	}
}

.anchor-el {
	&--target {
		position: relative;
		height: 0;
		z-index: -1;
		top: -$header-mob-h;
		@media (min-width: 768px) {
			top: -$header-desk-h;
		}
	}
}

.bg-cover {
	@include bg-cover;
}

.bg-contain {
	@include bg-contain;
}

.tl-main {
	padding-top: $header-mob-h;
  @media (min-width: 768px) {
		padding-top: $header-desk-h;
  }
}

.sticky-sidebar {
	@media (min-width: 768px) {
		&.sticky-md {
			position: sticky;
			top: $header-desk-h;
		}
	}
	@media (min-width: 1025px) {
		&.sticky-lg {
			position: sticky;
			top: $header-desk-h;
		}
	}
	@media (min-width: 1200px) {
		&.sticky-xl {
			position: sticky;
			top: $header-desk-h;
		}
	}
}

@media (min-width: 768px) {
	.tl-pad {
		padding-left: 2.5rem !important;
		padding-right: 2.5rem !important;
		&.mod-sm {
			padding-left: 1.25rem !important;
			padding-right: 1.25rem !important;
		}
	}

	.collapse {
		&.mob-only {
			display: block;
		}
	}
}