.post-hero {
  &--bg {
    padding-bottom: 33%;
  } 
}

.editor-content {
  h1, h2, h3, h4, h5, h6 {
    @include txt-h3;
    text-align: center;
    margin-bottom: 2rem;
    @media (min-width: 768px) {
      text-align: left;
    }
  }
  @include txt-p-big;
  img {
    max-width: 100%;
  }
}