// Colors
$cl-bg: var(--main-bg-color);
$cl-white: #fff;
$cl-grey: #DBD5CE; 
$cl-muted: #797671;
$cl-main: rgba(#000, 0.9);
$cl-bright: #171716;
$cl-line: #BEB4A8;

// Fonts 
$font-title: 'Canela', serif;
$font-main: 'AT Surt', sans-serif;

$font-h1: 'Lovelace', serif;
$font-h2: 'Akzidenz-Grotesk Pro Ext', sans-serif;
$font-tech: 'Akzidenz-Grotesk Pro', sans-serif;
$font-p-big: 'Lovelace Text', serif;

// Misc
$nav-drawer-content-height: calc(100vh - 50px);
$nav-drawer-width: calc(100vw - 69px);
$header-mob-h: 50px;
$header-desk-h: 95px;

$cart-drawer-mob-width: 307px;
$cart-drawer-mob-height: calc(100vh - 50px);

$cart-drawer-desk-width: 355px;
$cart-drawer-desk-height: 100vh;

// Mixins
@mixin btn-reset {
  text-decoration: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  &:focus {
    outline: none;
  }
}

@mixin btn-align {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  vertical-align: middle;
}

@mixin base-font {
  font-family: $font-tech;
  font-weight: 300;
  line-height: 1.5;
}

@mixin txt-h1 {
  font-family: $font-h1;
  font-weight: 200;
  font-size: 3.2rem;
  line-height: 1.1;
  letter-spacing: normal;
  word-wrap: break-word;
  @media (min-width: 768px) {
    font-size: 4rem;
  }
}

@mixin txt-h2 {
  font-family: $font-h2;
  font-size: 0.875rem;
  line-height: 1.5;
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

@mixin txt-h3 {
  font-family: $font-main;
  font-size: 1.625rem;
  line-height: 1.2;
}

@mixin txt-p {
  @include base-font;
  font-size: 1.125rem;
}

@mixin txt-p-big {
  font-family: $font-p-big;
  font-size: 1.4375rem;
  line-height: 1.5;
  letter-spacing: 0.02em;
}

@mixin txt-p-large {
  font-family: $font-p-big;
  font-size: 1.75rem;
  line-height: 1.25;
  letter-spacing: 0.01em;
  a {
    font-style: italic;
    display: inline-block;
    position: relative;
    text-decoration: none; 
    &:after {
      content: '';
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      border-top: 1px solid $cl-main;
    }
    &:hover {
      &:after {
        border-top: 1px solid transparent;
      }
    }
  }
  @media (min-width: 768px) {
    font-size: 2.625rem;
  }
} 

@mixin txt-main {
  @include base-font;
  font-size: 1rem;
  letter-spacing: 0.03em;
}

@mixin txt-h-span {
  font-family: $font-h2;
  font-size: 2rem;
  line-height: 1.5;
  text-transform: uppercase; 
  @media (min-width: 768px) {
    font-size: 2.75rem;
  }
}

@mixin input {
  @include txt-main;
  display: block;
  width: 100%;
  padding: 1rem;
  color: $cl-main;
  background-color: $cl-grey;
  background-clip: padding-box;
  border: 1px solid $cl-line;
}

@mixin nav-blocks-sizes {
  position: fixed;
  z-index: 801;
  left: 0;
  //top: $header-mob-h;
  top: 0;
  height: $nav-drawer-content-height;
  max-height: $nav-drawer-content-height;
  width: $nav-drawer-width;
  overflow-y: auto;
}

@mixin trn-drawer {
  transition: transform 0.2s cubic-bezier(0.645, 0.045, 0.355, 1), margin-left 0.2s cubic-bezier(0.645, 0.045, 0.355, 1), visibility 0.2s cubic-bezier(0.645, 0.045, 0.355, 1), box-shadow 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
}

@mixin trn-03 {
  transition: all 0.3s ease;
}

@mixin trn-05 {
  transition: all 0.5s ease;
}

@mixin bg-cover {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

@mixin bg-contain {
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

@mixin list-reset {
  padding: 0;
	margin: 0;
	list-style: none;
}