input[type=submit]:not(.tl-btn),
input[type=button]:not(.tl-btn),
input[type=reset]:not(.tl-btn),
button:not(.tl-btn) {
  @include btn-reset;
}

.tl-btn {
  @include txt-h2;
  line-height: 1.4;
  text-transform: uppercase;
  min-width: 8.5rem;
  padding: 0.375rem 1.25rem;
  border-radius: 0.1875rem;
  @include btn-reset;
  @include btn-align;
  @include trn-05;
  &:hover {
    text-decoration: none;
    opacity: 0.85;
  }
  &--big {
    font-family: $font-h2;
    padding: 1.1rem 2rem;
    min-width: 11.75rem;
  }
  &--main {
    color: $cl-main;
    border-color: $cl-grey;
    background-color: $cl-grey;
  }
  &--dark {
    color: rgba($cl-white, 0.9);
    border-color: $cl-bright;
    background-color: $cl-bright;
  }
  &--muted {
    color: $cl-white;
    border-color: $cl-line;
    background-color: $cl-line;
  }
  &--outline {
    color: $cl-main;
    border-color: rgba(#000, 0.15);
    background-color: $cl-grey;
  }
  &.auto-width {
    min-width: 0;
    padding-left: 0.75rem;
    padding-right: 0.75rem;
  }
  &.mob-auto-width {
    @media (max-width: 768px) {
      min-width: 0;
      padding: 0.375rem;
    }
  }
}

.btn-link {
  @include txt-h2;
  @include btn-reset;
  @include btn-align;
  text-decoration: underline;
  cursor: pointer;
  &.no-line {
    text-decoration: none;
  }
  &:hover {
    text-decoration: none;
    opacity: 0.85;
  }
  &.active {
    background-color: $cl-grey;
    border-color: $cl-grey;
  }
}

.btn-toggler {
  @include btn-reset;
  @include btn-align;
  padding: 0;
  width: 1.5rem;
  height: 1.5rem;
  position: relative;
  &--line {
    display: inline-block;
    width: 100%;
    height: 0;
    border-top: 1px solid $cl-main;
    position: absolute;
    left: 0;
    &.l-1 {
      top: 0.375rem;
      transition: top 0.2s 0.3s, transform 0.2s 0.3s;
    }
    &.l-2 {
      bottom: 0.375rem;
      transition: bottom 0.2s 0.3s, transform 0.2s 0.3s;
    }
  }
}

.tl-header.mod-open {
  .btn-toggler {
    .btn-toggler--line {
      &.l-1 {
        top: 0.675rem;
        transform: rotate(45deg);
      }
      &.l-2 {
        bottom: 0.675rem;
        transform: rotate(-45deg);
      }
    }
  }
}

.btn-close {
  @include btn-reset;
  display: inline-flex;
  padding: 0;
  width: 1.5rem;
  height: 1.5rem;
  position: relative;
  &--line {
    display: inline-block;
    width: 100%;
    height: 0;
    border-top: 1px solid $cl-main;
    position: absolute;
    left: 0;
    &.l-1 {
      top: 0.675rem;
      transform: rotate(45deg);
    }
    &.l-2 {
      bottom: 0.675rem;
      transform: rotate(-45deg);
    }
  }
  &.mod-small {
    width: 0.75rem;
    height: 0.75rem;
    .btn-close--line {
      &.l-1 {
        top: 0.28125rem;
      }
      &.l-2 {
        bottom: 0.28125rem;
      }
    }
  }
}