.tile-138 {
  padding-bottom: 138%;
}

.tile-128 {
  padding-bottom: 128%;
}

.tile-100 {
  padding-bottom: 100%;
}

.tile-70 {
  padding-bottom: 70%;
}

.embed-video {
  position: relative;
  padding-bottom: 56.25%;
  iframe {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
}

@media (max-width: 767px) {
  .tile-pad {
    width: 80%;
    &.odd {
      margin-right: 20%;
    }
    &.even {
      margin-left: 20%;
    }
  }
}