$cl-white: #fff;
$cl-grey: #EAE9E0;
$cl-muted: #A6ABB0;
$cl-main: rgba(#000, 0.9);
$cl-bright: #171716;
$cl-line: #D7D5C7;

$font-main: 'AT Surt', sans-serif;

@mixin base-font {
  font-family: $font-main;
  font-weight: 300;
}

@mixin txt-main {
  @include base-font;
  font-size: 0.625rem;
  letter-spacing: 0.03em;
  line-height: 1.5;
  @media (min-width: 768px) {
    font-size: 0.75rem;
  }
}

@mixin input {
  @include txt-main;
  display: block;
  width: 100%;
  padding: 1rem;
  color: $cl-main;
  background-color: $cl-grey;
  background-clip: padding-box;
  border: 1px solid $cl-line;
}

.select2-container {
  text-align: left;
  box-sizing: border-box;
  text-transform: initial;

  display: inline-block;
  margin: 0;
  position: relative;
  vertical-align: middle;

  @import "single";
  @import "multiple";
}

@import "dropdown";

.select2-close-mask {
  border: 0;
  margin: 0;
  padding: 0;
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  min-height: 100%;
  min-width: 100%;
  height: auto;
  width: auto;
  opacity: 0;
  z-index: 99;

  // styles required for IE to work

  background-color: $cl-grey;
  filter: alpha(opacity=0);
}

.select2-hidden-accessible {
  border: 0 !important;
  clip: rect(0 0 0 0) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}

@import "theme/default/layout";
// @import "theme/classic/layout";
