.hero {
  position: relative;
  &-bg {
    min-height: 56.25vw;
  }
  &-content {
    background-color: $cl-grey;
    @media (min-width: 768px) {
      background-color: transparent;
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
    }
  }
}