.tl-header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background-color: $cl-bg;
  z-index: 777;
  height: $header-mob-h;
  @media (min-width: 768px) {
    height: $header-desk-h;
  }
  &--container {
    @media (min-width: 768px) {
      height: 100%;
    } 
  }
  &.mod-open {
    .nav-drawer {
      visibility: visible;
      transform: translateX(0);
      &--backdrop {
        display: block;
      }
    }
  }
  ul, li {
    list-style: none;
    padding: 0;
    margin-bottom: 0;
  }
  a {
    text-decoration: none;
  }
  &--logo {
    img {
      max-width: 100%;
      opacity: 0.9;
    }
  }
  &--menu {
    > .nav-item {
      padding-top: 1rem;
      padding-bottom: 1rem;
      > .nav-link {
        display: block;
        position: relative;
        z-index: 777;
        color: $cl-main;
        padding: 0 0.5rem;
        @include txt-h2;
        @include trn-03;
      }
      &.active,
      &.current_page_parent {
        > .nav-link {
          &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0.5rem;
            right: 0.5rem;
            border-top: 1px solid $cl-line;
          }
        }
      }
      &.has-megamenu {
        cursor: pointer;
      }
    }
    .nav-item {
      position: relative;
      .tl-dropdown--menu {
        transform: translateY(0.5rem);
        &.mod-currencies a,
        .nav-link {
          padding: 0.5rem 0;
          display: block;
        }
      }
      &:hover {
        z-index: 799;
        .tl-dropdown--menu {
          transform: translateY(0);
          opacity: 1;
          visibility: visible;
        }
      }
    }
    .nav-link {
      text-decoration: none;
      &.dropdown-item {
        display: block;
      }
    }
  }
  &--counter {
    @include txt-h2;
    cursor: pointer;
  }
}

.tl-dropdown {
  &--menu {
    opacity: 0;
    visibility: hidden;
    background-color: $cl-bg;
    position: absolute;
    min-width: 12rem;
    @include trn-03;
    padding: 0.5rem;
    padding-top: calc(0.5rem + 43px);
    left: 0;
    top: 100%;
    &.parent-width {
      min-width: 100%;
    }
  }
}

.tl-megamenu {
  position: fixed;
  z-index: 777;
  top: $header-desk-h;
  left: 0;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  transform: translateY(0.5rem);
  border-top: 1px solid $cl-line;
  border-bottom: 1px solid $cl-line;
  background-color: $cl-bg;
  @include trn-03;
  &--banner {
    &-col {
      padding-top: 1.25rem;
      padding-bottom: 1.25rem;
      @media (min-width: 768px) {
        flex: 0 0 33.333333%;
        max-width: 33.333333%;
      }
      @media (min-width: 1200px) {
        flex: 0 0 250px;
        max-width: 250px;
      }
    }
  }
  &--product {
    &-img {
      width: 40%;
      min-height: 240px;
      @media (min-width: 992px) {
        width: 228px;
        min-height: 310px;
      }
    }
    &-content {
      width: 60%;
      @media (min-width: 992px) {
        width: calc(100% - 228px);
      }
    }
  }
}

body:not(.touch-device) .has-megamenu:hover,
body.touch-device .has-megamenu.sub-open {
  > .tl-megamenu {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
  }
}

// Mobile menu
.nav-drawer {
  position: fixed;
  left: 0;
  top: $header-mob-h;
  z-index: 800;
  height: $nav-drawer-content-height;
  width: $nav-drawer-width;
  border-top: 1px solid $cl-line;
  background-color: $cl-bg;
  visibility: hidden;
  transform: translateX(-100%);
  @include trn-drawer;
  overflow: hidden;
  &--backdrop {
    display: none;
    position: fixed;
    z-index: 801;
    width: 69px;
    top: $header-mob-h;
    right: 0;
    bottom: 0;
    cursor: pointer;
    background-color: $cl-line;
    opacity: 0.9;
  }
  &--body {
    height: 100%;
  }
  .nav-menu {
    // @include nav-blocks-sizes;
    height: auto;
    .menu-item {
      padding: 0.75rem 0;
      &.mod-currencies {
        padding: 0;
        a {
          @include txt-h2;
          display: block;
          padding: 0.75rem 0;
        }
      }
    }
    > .menu-item {
      > .nav-item {
        @include txt-h2;
      }
    }
    .sub-menu--title {
      > .nav-item {
        position: relative;
        padding: 0.75rem 0;
      }
    }
    .nav-item {
      cursor: pointer;
      display: flex;
      align-items: center;
      text-decoration: none;
      @include txt-h2;
    }
    .sub-menu {
      @include nav-blocks-sizes;
      @include trn-03;
      margin-left: 100%;
      background-color: $cl-bg;
      &.mod-open {
        margin-left: 0;
      }
    }
  }
}

.nav-arrow {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 1rem;
  height: 1rem;
  svg {
    width: 100%;
    height: 100%;
  }
}